@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;background-color:#f2f2f2;font-family:"Lexend Deca","Big Shoulders Display",sans-serif}.container{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.content{display:flex;border-radius:10px;width:80%;color:#f2f2f2;box-shadow:0px 0px 30px 0px rgba(0,0,0,0.5)}.card>h2{text-transform:uppercase;font-family:'Big Shoulders Display', sans-serif}.card>div,.card>h2,.card>p,.card>span,.card>span>img{background-color:transparent}.card{display:flex;flex-direction:column;padding:30px}.card.card__orange{background-color:#e38826;border-bottom-left-radius:10px;border-top-left-radius:10px}.card.card__cyan{background-color:#006970}.card.card__dark__cyan{background-color:#004241;border-bottom-right-radius:10px;border-top-right-radius:10px}span.card__icon,h2.card__title,p.card__description{margin-bottom:20px}p.card__description{height:115px;font-size:15px;color:rgba(255,255,255,0.75)}div.card__footer{margin-top:50px;margin-bottom:15px}button.btn{cursor:pointer;background-color:#f2f2f2;border:2px solid #f2f2f2;border-radius:30px;padding:10px 20px 10px 20px;font-weight:bold}button.btn.btn__orange{color:#e38826}button.btn.btn__cyan{color:#006970}button.btn.btn__dark__cyan{color:#004241}button.btn:hover{color:#f2f2f2;border:2px solid #f2f2f2;background-color:transparent;font-weight:unset}@media (min-width: 1440px) and (max-width: 3000px){.content{width:999px}}@media (min-width: 0px) and (max-width: 600px){.container{width:auto;height:auto;padding-top:30px;padding-bottom:30px}.content{flex-direction:column}.card.card__orange{border-top-left-radius:10px;border-top-right-radius:10px;border-bottom-left-radius:0px}.card.card__dark__cyan{border-bottom-right-radius:10px;border-top-right-radius:0px;border-bottom-left-radius:10px}}

